<template>
    <div>
        <div data-v-64b0f2f5="" class="order-list" v-for="(item,index) in orders" :key="index">
            <div data-v-64b0f2f5="" class="ordernum-wrap">
                <div data-v-64b0f2f5="" class="ordernum">订单编号：{{item.ordernum}}</div>
                <div data-v-64b0f2f5="" class="status">{{item.status==='0'?'待付款':item.status==='1'?'待收货':'已收货'}}</div>
            </div>
            <div data-v-64b0f2f5="" @click="$router.push('/user/order/details?ordernum='+item.ordernum)"
                 class="item-list" v-for="(item2,index2) in item.goods" :key="index2">
                <div data-v-64b0f2f5="" class="image">
                    <img data-v-64b0f2f5="" :src="item2.image" alt="">
                </div>
                <div data-v-64b0f2f5="" class="title">{{item2.title}}</div>
                <div data-v-64b0f2f5="" class="amount">x {{item2.amount}}</div>
            </div>
            <div data-v-64b0f2f5="" class="total-wrap">
                <div data-v-64b0f2f5="" class="total">实付金额：¥{{item.total}}</div>
                <div data-v-64b0f2f5="" class="status-wrap">
                    <div data-v-64b0f2f5="" @click="cancelOrder(index,item.ordernum)" class="status-btn"
                         v-if="item.status==='0'">取消订单
                    </div>
                    <div data-v-64b0f2f5="" @click="sureOrderPage(index,item)" class="status-btn">
                        {{item.status==='0'?'去付款':item.status==='1'?'确认收货':'已收货'}}
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapState} from "vuex"
    import Vue from 'vue';
    import {Dialog} from 'vant';
    import UpRefresh from '../../../assets/js/libs/uprefresh';
    // 全局注册
    Vue.use(Dialog);
    export default {
        mounted(){
            document.title=this.$route.meta.title;
        },
        created() {
            //安全认证
            this.$utils.safeUser(this);
            //创建分页实例
            this.pullUp = new UpRefresh();

            this.status = this.$route.query.status ? this.$route.query.status : "all";
            //我的订单
            this.goData();
            console.log(this.status)
        },
        computed: {
            ...mapState({
                //我的订单
                orders: state => state.order.orders
            })
        },

        methods: {
            //我的订单
            goData() {
                //我的订单

                this.myOrder({
                    status: this.status, page: 1, success: (pageNum) => {
                        //分页功能
                        this.pullUp.init({"curPage": 1, "maxPage": parseInt(pageNum), "offsetBottom": 100}, (page) => {
                            this.myOrderPage({status: this.status, page: page});
                        });
                    }
                });
            },
            //取消订单
            cancelOrder(index, ordernum) {
                Dialog.confirm({
                    title: '标题',
                    message: '确定要取消吗?',
                })
                    .then(() => {
                        // on confirm
                        this.clearOrder({ordernum: ordernum, index: index})
                    })
                    .catch(() => {
                        // on cancel
                    });

            },
            //确认订单
            sureOrderPage(index, item) {
                if (item.status != '2') {
                    this.sureOrder({ordernum: item.ordernum, index: index, status: 2})
                }
            },
            ...mapActions({
                //我的订单
                myOrder: "order/myOrder",
                //取消订单
                clearOrder: "order/clearOrder",
                //确认订单
                sureOrder: "order/sureOrder",
                //订单分页
                myOrderPage: "order/myOrderPage"
            })
        },
        beforeDestroy() {
            this.pullUp.uneventSrcoll();
        },
        beforeRouteUpdate(to, from, next) {
            this.status = to.query.status;
            //我的订单
            this.goData();
            //确认订单

            console.log(this.status)
            next()
        }
    }
</script>

<style scoped>
    .order-list {
        width: 100%;
        height: auto;
        background: #FFF;
    }

    .order-list .ordernum-wrap {
        width: 96%;
        height: 0.8rem;
        border-bottom: #EFEFEF 1px solid;
        display: flex;
        display: -webkit-flex;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        padding-left: 2%;
        padding-right: 2%;
        align-items: center;
        -webkit-align-items: center;
    }

    .order-list .ordernum-wrap .ordernum, .order-list .ordernum-wrap .status {
        width: auto;
        height: auto;
        font-size: 0.28rem;
    }

    .item-list {
        width: 100%;
        height: 1.6rem;
        border-bottom: #EFEFEF 1px solid;
        position: relative;
        z-index: 1;
    }

    .item-list .image {
        width: 1.2rem;
        height: 1.2rem;
        position: absolute;
        z-index: 1;
        left: 2%;
        top: 0.2rem;
    }

    .item-list .image img {
        width: 100%;
        height: 100%;
    }

    .item-list .title {
        width: 72%;
        height: auto;
        font-size: 0.28rem;
        position: absolute;
        z-index: 1;
        left: 22%;
        top: 0.2rem;
    }

    .item-list .amount {
        width: auto;
        height: auto;
        position: absolute;
        z-index: 1;
        right: 2%;
        bottom: 0.2rem;
        font-size: 0.24rem;
        color: #909090;
    }

    .total-wrap {
        width: 96%;
        height: 0.8rem;
        border-bottom: 1px solid #F17F1F;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        padding-left: 2%;
        padding-right: 2%;
    }

    .total-wrap .total {
        font-size: 0.28rem;
    }

    .total-wrap .status-btn {
        font-size: 0.24rem;
        width: 1.24rem;
        height: 0.52rem;
        line-height: 0.52rem;
        text-align: center;
        border: 1px solid #f15353;
        border-radius: 4px;
        margin-left: 0.2rem;
    }

    .total-wrap .status-wrap {
        display: flex;
        width: auto;
        justify-content: flex-end;
    }

</style>